html, body {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  @include NeutrifStudio_Regular();
  @include font-size(18);
  @include media-breakpoint-up(md) {
    @include font-size(16);
  }
}
.content-wrap {
  flex: 1 0 auto;
}

.main {
  @include media-breakpoint-up(md) {
    padding-top: 3rem;
  }
  padding-bottom: 3rem;
}
.main-wrapper {
  background-color: map-get($supporting-colors, 'lightg');
}


p, ol, ul, dl {
    margin-bottom: 1.7em;
}

h3,
h4 {
    @include NeutrifStudio_Bold();
}

h1 {
  @include PortraitText-Medium-Web();
  @include font-size(36);
  @include media-breakpoint-up(sm) {
    @include font-size(40);
  }
  @include media-breakpoint-up(lg) {
    @include font-size(48);
  }
}
h2 {
  @include PortraitText-Medium-Web();
  @include font-size(24);
  @include media-breakpoint-up(sm) {
    @include font-size(30);
  }
  @include media-breakpoint-up(lg) {
    @include font-size(36);
  }
}

h3 {
  @include font-size(18);
  @include media-breakpoint-up(sm) {
    @include font-size(20);
  }
  @include media-breakpoint-up(lg) {
    @include font-size(24);
  }
}
h4 {
  @include font-size(18);
}

img, figure {
  max-width: 100%;
  height: auto;
}

.bold, b, strong {
    @include NeutrifStudio_Bold();
}

i, em {
    @include NeutrifStudio_RegularItalic();
}

i strong, i bold, em strong, em bold,
strong i, bold i, strong em, bold em {
  @include NeutrifStudio_BoldItalic();
}

blockquote {
  @include Portrait-RegularItalic-Web();
  @include font-size(19);
  position: relative;
  margin-bottom: 1.7rem;
  @include media-breakpoint-up(md) {
    width:80%;
    margin: 0 auto 1.7rem auto;
  }
  /*&:before {
    content: "\201C";
    position: absolute;
    left: -10px;
  }*/
  p {
    margin-bottom: 0;
  }

}

/* Default link styles */
a:link, a:visited {
    text-decoration: none;
    color: map-get($theme-colors, 'primary');
}
a:hover, a:active, a:focus {
    text-decoration: underline;
    color: map-get($theme-colors, 'navyblack');
}


.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

.video-container iframe,  
.video-container object,  
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

iframe{
  border:0;
}

.tooltip {
  @include font-size(12);
}
.inline-list {
  list-style: none;
  padding: 0;
  li {
    display: inline-block;
  }
}
.section-padding {
  padding-top: 3rem;
  padding-bottom: 3rem;
}

#CybotCookiebotDialog {
  -webkit-box-shadow: 0 -1px 10px 0 rgba(0,0,0,0.1) !important;
  box-shadow: 0 -1px 10px 0 rgba(0,0,0,0.1) !important;
}
#CybotCookiebotDialogBodyButtonAccept {
  @extend .btn;
  @extend strong;
  padding-bottom: 12px !important;
  border: none !important;
  &:hover {
    border:none !important;
  }
}


